import styled, { injectGlobal } from 'styled-components';
import { device, size } from './breakpoints';
import { Button } from 'antd';

injectGlobal`
.bio-wrap {
  visibility: hidden;
  opacity: 0;
  transition: 250ms;

  &.visible {
    visibility: visible;
    opacity: 1;
    transition: 250ms;
  }
}

.become-opaque-on-hover {
  opacity: 1;
  transition: 250ms;

  &:hover {
    opacity: 0.75;
    transition: 250ms;
  }
}


iframe {
  width: 100%;
  height: 80rem;
  border: 0px;
  margin-top: 2rem;
}

.orange-link {
  color: #fff;
  font-size: 24px;
  font-weight: 700;
  background-color: #0E121A;
  padding: 7px 12px;
  border-radius: 100px;
}

.orange-link:hover {
  color: #fff;
  background-color: #232C38;
}

#market .ant-message-notice-content {
  background: white;
  color: black;
}

.ant-select-dropdown-menu-item:hover {
  background-color: #e6f7ff !important;
}
.ant-select-dropdown-menu-item-disabled:hover {
  background-color: #fff !important;
}
.ant-select-dropdown-menu-item-selected,
.ant-select-dropdown-menu-item-selected:hover {
  background-color: #fafafa !important;
}
.ant-select-dropdown-menu-item-active {
  background-color: #e6f7ff !important;

.explainer-modal .ant-modal-close-x {
  color: white;
}
`;

const MarketHeader = styled<{ align?: string }, 'h2'>('h2')`
  font-size: 36px;
  font-weight: 500;
  line-height: 1.1;
  text-align: ${props => props.align || ''};

  @media (max-width: ${size.laptop}) {
    font-size: 26px;
  }

  @media ${device.laptop} and (max-width: 1120px) {
    font-size: 28px;
  }
`;

const MarketText = styled.p`
  font-size: 18px;
  font-weight: 300;

  @media (max-width: ${size.laptop}) {
    font-size: 14px;
  }

  @media ${device.desktopS} {
    font-size: 22px;
  }
`;

const PrivacyBulletList = styled.li`
  padding: 5px 0;
  list-style: none;
  font-weight: 300;
  margin-left: 30px;
  font-size: 16px;
  :before {
    content: '';
    line-height: 1em;
    width: 10px;
    height: 10px;
    background-color: #00e2c1;
    float: left;
    margin-top: 6px;
    border-radius: 50%;
    margin-right: 10px;
    margin-left: -20px;
  }
`;

const MarketList = styled.li`
  font-size: 18px;
  padding: 10px 0;
  list-style: none;
  font-weight: 300;
  margin-left: 20px;
  :before {
    content: '';
    line-height: 1em;
    width: 10px;
    height: 10px;
    background-color: #00e2c1;
    float: left;
    margin-top: 10px;
    border-radius: 50%;
    margin-right: 10px;
    margin-left: -20px;
  }
  @media (max-width: ${size.laptop}) {
    font-size: 14px;
    :before {
      margin-top: 5px;
    }
  }

  @media ${device.desktopS} {
    font-size: 22px;
  }
`;

const MarketDescriptionText = MarketText.extend`
  font-size: 25px;

  @media ${device.mobileS} {
    font-size: 16px;
  }
  @media ${device.tablet} {
    font-size: 22px;
  }
`;

const MarketDescriptionWrapper = styled.section`
  padding: 50px;

  @media ${device.mobileL} and (max-width: ${size.tablet}) {
    padding: 70px;
  }

  @media ${device.tablet} {
    padding: 100px;
  }

  @media ${device.laptopL} {
    padding: 100px 150px;
  }

  @media ${device.desktopS} {
    padding: 150px 350px;
  }
`;

const BioModalContentWrap = styled.div`
  width: 515px;

  @media (max-width: ${size.tablet}) {
    width: 350px;
  }
`;

const SectionWrapper = styled.section`
  padding: 70px;

  @media ${device.mobileS} and (max-width: ${size.mobileL}) {
    padding: 0px;
  }

  @media ${device.mobileL} and (max-width: ${size.laptop}) {
    padding: 50px;
  }

  @media ${device.desktopS} {
    padding: 100px 150px;
  }

  @media ${device.desktopM} {
    padding: 100px 250px;
  }
`;

const HeroText = styled.div`
  color: #f0f0f0;
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 30px;
  line-height: 1.2;

  @media ${device.mobileS} {
    font-size: 28px;
    margin-bottom: 0px;
  }

  @media ${size.mobileS} and (max-width: ${size.tablet}) {
    font-size: 38px;
    margin-bottom: 0px;
  }

  @media ${device.tablet} {
    font-size: 38px;
    margin-bottom: 30px;
  }

  @media ${device.laptop} {
    font-size: 60px;
  }
`;

const TextWrapper = styled.div`
  padding: 0 60px 70px 60px;

  @media (max-width: ${size.mobileL}) {
    padding: 0 20px 50px 20px;
  }
`;

const ContentWrapper = styled.p`
    font-size: 16px
    font-weight: 300;
    line-height: 1.5rem;
    text-align: justify;
`;

const MarketButton = styled<{ bg: string; color: string }, Button>(Button)`
  && {
    min-width: 140px;
    font-weight: 800
    font-size: 20px;
    padding: 0 30px;
    background-color: ${props => props.bg};
    color: ${props => props.color};
    border: none;
  }

  &&:hover {
    opacity: 0.8;
  }
`;

const Section = styled<{ bg: string; height?: string }, 'section'>('section')`
  background: ${props => props.bg};
  height: ${props => props.height}px;
  padding: 55px 0 70px 0;
  width: 100%;

  @media (max-width: 768px) {
    padding: 50px 25px;
  }
`;

const ContainerWrapper = styled.div`
  margin: auto;
  height: inherit;

  @media (min-width: 576px) {
    max-width: 540px;
  }

  @media ${device.tablet} {
    max-width: 720px;
  }

  @media ${device.laptop} {
    max-width: 960px;
  }

  @media ${device.laptopM} {
    max-width: 1120px;
  }
`;

const SectionHeader = styled<{ color: string }, 'h2'>('h2')`
  font-size: 36px;
  color: ${props => props.color || '#000'};
  letter-spacing: -0.45px;
  line-height: 38px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 30px;

    @media (max-width: 1024px) {
      font-size: 32px;
    }
  }
`;

const Description = styled<{ color: string }, 'p'>('p')`
  font-size: 20px;
  color: ${props => props.color || '#000'};
  margin-bottom: 0;
  font-weight: 400;

  @media (max-width: 1024px) {
    font-size: 16px;
  }
`;

const SecondaryHeader = styled.h2`
  font-size: 22px;
  font-weight: 500;
  line-height: 28px;
`;

const SecondaryDescription = styled.p`
  font-size: 16px;
  color: #181e26;
  letter-spacing: 0;
  text-align: center;
  line-height: 22px;
  font-weight: 400;
`;

const UseCaseIllustration = styled.div`
  background: #00e2c1;
  margin-bottom: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  img {
    width: 100%;
  }

  span {
    position: absolute;
    top: 0px;
    left: 15px;
    background: black;
    color: white;
    padding: 5px 10px;
    font-weight: 700;
  }

  @media (max-width: 991px) {
    img {
      width: 250px;
    }
  }
`;

const HeroHeader = styled.h1`
  font-size: 42px;
  font-weight: 800;
  color: white;
  line-height: 44px;
  margin-bottom: 15px;

  @media (max-width: 1024px) {
    font-size: 36px;
    letter-spacing: -0.5px;
  }
`;

const HeroDescription = styled.p`
  font-size: 20px;
  font-weight: 300;
  color: #fff;
  margin-bottom: 35px;
  line-height: 28px;

  @media (max-width: 1024px) {
    font-size: 18px;
  }

  @media (max-width: 767px) {
    font-size: 16px;
    line-height: 22px;
  }
`;

export {
  MarketList,
  MarketHeader,
  MarketText,
  HeroText,
  BioModalContentWrap,
  SectionWrapper,
  MarketDescriptionText,
  MarketDescriptionWrapper,
  TextWrapper,
  ContentWrapper,
  MarketButton,
  Section,
  ContainerWrapper,
  SectionHeader,
  Description,
  SecondaryHeader,
  SecondaryDescription,
  UseCaseIllustration,
  HeroHeader,
  HeroDescription,
  PrivacyBulletList
};



